body {
    overflow-x: hidden;
}

.greenBtn {
    width: 225px;
    height: 50px;
    background-color: #43FFD2;
    color: #040A0A;
    text-align: center;
    font-family: 'Oswald';
    font-size: 12px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: 1.2px;
    text-transform: uppercase;
    transition: .35s;
    z-index: 4;
    &:hover {
        background-color: #7D2253;
        color: #fff;
    }
    &:focus {
        outline: none;
        border: 1px solid #7D2253;
    }
    &:active {
        background: #7D2253;
        color: #fff;
        box-shadow: 0px 0px 10px 0px #1B000E inset;
    }
}

.greenInput {
    border: 1px solid #43FFD2;
    background-color: transparent;
    color: #395959;
    font-family: 'Oswald';
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 0.56px;
    text-transform: uppercase;
    padding: 20px;
    outline: none;
}

$brackpoints: (
    sm: 500px,
    md: 750px,
    lg: 1100px,
    xl: 1200px,
);

@mixin sm-u(){
    @media (min-width: map-get($brackpoints, sm)){
        @content;
    }
};

@mixin md-u(){
    @media (min-width: map-get($brackpoints, md)){
        @content;
    }
};

@mixin lg-u(){
    @media (min-width: map-get($brackpoints, lg)){
        @content;
    }
};

// ONLY

@mixin sm(){
    @media (max-width: map-get($brackpoints, md) - .2px) and (min-width: map-get($brackpoints, sm)){
        @content;
    }
};

@mixin md(){
    @media (max-width: map-get($brackpoints, lg) - .2px) and (min-width: map-get($brackpoints, md)){
        @content;
    }
};

@mixin lg(){
    @media (max-width: map-get($brackpoints, xl) - .2px) and (min-width: map-get($brackpoints, lg)){
        @content;
    }
};

@mixin xl(){
    @media (min-width: map-get($brackpoints, xl)){
        @content;
    }
};

//Mobile first

@mixin xl-d(){
    @media (max-width: map-get($brackpoints, xl) - .2px){
        @content
    }
};

@mixin lg-m-d(){
    @media (max-width: map-get($brackpoints, lg-m) - .2px){
        @content
    }
};

@mixin lg-d(){
    @media (max-width: map-get($brackpoints, lg) - .2px){
        @content
    }
};

@mixin md-d{
    @media (max-width: map-get($brackpoints, md) - .2px){
        @content
    }
};

@mixin sm-d{
    @media (max-width: map-get($brackpoints, sm) - .2px){
        @content
    }
};

.wrapper {
    width: 1109px;
    margin: -1px auto;
    padding-top: 100px;
    @include lg-d {
        width: 92%;
    }
}

.whiteBtn {
    width: 225px;
    height: 50px;
    background-color: transparent;
    color: #fff;
    border: 1px solid #fff;
    cursor: pointer;
    text-align: center;
    font-family: 'Oswald';
    font-size: 12px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: 1.2px;
    text-transform: uppercase;
    transition: .35s;
    &:hover {
        background-color: #43FFD2;
        border-color: #43FFD2;
        color: #000;
    }
    &:focus {
        background: transparent;
        outline: none;
        color: #fff;
        border: 1px solid #43FFD2;
    }
    &:active {
        color: #43FFD2;
        color: #000;
        background-color: #43FFD2;
        box-shadow: 0px 0px 10px 0px #01281F inset;
    }

    @include md-d {
        width: 180px;
        height: 55px;
    }
}

.btnDisabled {
    border: 1px solid #555;
    background: transparent;
    color: #555;
    cursor: auto;
    &:hover {
        border: 1px solid #555;
        background: transparent;
        color: #555;
    }
    &:focus {
        border: 1px solid #555;
        background: transparent;
        color: #555;
    }
    &:active {
        border: 1px solid #555;
        background: transparent;
        color: #555;
    }
}

.input,
.correctInput,
.wrongValue,
.unactive {
    width: 540px;
    @include md-d {
        width: 280px;
    } 

    height: 60px;
    border: 1px solid #555;

    color: #555;
    font-family: 'Oswald';
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 0.56px;
    background-color: transparent;
    padding: 20px;
    margin-bottom: 10px;
}

.correctInput {
    border: 1px solid #fff;
    color: #fff;
}

.wrongValue {
    display: block;
    border: 1px solid #FF3A44;
    color: #FF3A44;
    position: relative;
    display: flex;
    justify-content: space-between;
}

.label {
    position: relative;
    width: 540px;
}

.errorMessage {
    color: #FF3A44;
    font-family: 'Oswald';
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 0.56px;
    background-color: transparent;
    margin-top: -10px;
    max-width: 540px;
}

.exlamation {
    position: absolute;
    top: 0;
    right: -10px;
    transform: translate(0px, 143%);
}

.unactive {
    border: 1px solid #212121;
    color: #212121;
}